import React,{useState,useEffect} from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import ProductsTable from './OrderTable';
import firebase from 'firebase'

const StudnetOderInfo = props => {

  const [currentlyLending,setCurrentlyLending] = useState([])

  

  useEffect(() => {
      if(currentlyLending.length === 0){
        var docRef = firebase.firestore().collection("users").doc(firebase.auth().currentUser.uid);

        docRef.get().then(function(doc) {
          if (doc.exists) {
            if(doc.data().currentlyLending !== undefined){
              setCurrentlyLending(doc.data().currentlyLending)
            
            }
          } else {
              console.log("No such document!");
          }
        }).catch(function(error) {
          console.log("Error getting document:", error);
        });
      }
  }, []);
  
  return (
    <Grid item xs={12}>
        <Typography component="h3" variant="h5" color="primary" gutterBottom>Order Details</Typography>
        <ProductsTable products={currentlyLending} />
    </Grid>
  );
};

StudnetOderInfo.propTypes = {
  products: PropTypes.array,
};

const mapStateToProps = state => {
  const {
    firestore: {
      ordered: { products },
    },
  } = state;
  return {
    products,
  };
};

export default connect(mapStateToProps)(StudnetOderInfo);
